.addformation>.head>.icongroup>span,
.addthingformation>.head>.icongroup>span {
  display: inline-block;
  padding-left: 25px
}

.addformation>.content,
.addthingformation>.content {
  height: 96px;
  /*width: calc(100% - 8px);*/
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.addthingformation>.content {
  height: 192px;
}

.addthingformation.car>.content {
  height: 48px;
}

.addformation>.content>div:nth-child(2),
.addthingformation>.content>div:nth-child(2) {
  width: 10%;
  text-align: center;
  height: 90%;
  background: #fe0000;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.buttongroup {
  width: 100%;
  height: 70px;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttongroup>button {
  width: 40%;
  height: 40px;
  line-height: 40px;
  letter-spacing: 10px;
  text-indent: 10px;
  margin: 0 20px;
}

.buttongroup>button:nth-child(1) {
  background: #26a2ff
}

.buttongroup>button:nth-child(2) {
  background: transparent;
  color: #26a2ff;
  border: 1px solid #26a2ff
}

.poup>.poupHead {
  /*height: 20%;*/
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #26a2ff;
  color: white;
  box-sizing: border-box;
  border: 8px solid transparent
}

.poup>.poupContent {
  /*height: 80%;*/
  height: calc(100% - 40px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.poup>.poupContent>button {
  background: transparent;
  color: #26a2ff;
  border: 1px solid #26a2ff;
  /*margin-top: -60px!important;*/
}

.poup>.poupContent>button:nth-child(1) {
  width: 35%;
  margin-right: 5%;
}

.poup>.poupContent>button:nth-child(2) {
  width: 35%;
  margin-left: 5%
}

.poup>.poupHead {
  background: transpartent
}

.poupSearch>.poupHead {
  border: none;
}

.poupSearch .mint-navbar {
  width: 100%;
  background: #26a2ff
}

.fa-search {
  height: 48px;
  width: 20px;
  line-height: 48px;
  color: black;
}

.poupSearch .fa-remove {
  position: absolute;
  right: 4px;
  top: 0px;
  height: 40px;
  width: 20px;
  /*background: red;*/
  line-height: 40px;
  text-align: center;
}

.poupSearch .mint-tab-container-wrap {
  /*margin-top: 20px;*/
}

.poupSearch .mint-tab-container {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow: scroll;
  border-left: 8px solid white;
  border-right: 8px solid white;
}

.poupSearch ul {
  border-bottom: 1px solid #c8c8cd;
  height: 48px;
}

.poupSearch.namePoup ul {
  border-bottom: 1px solid #c8c8cd;
  height: 72px;
}

.poupSearch.namePoup .useHistory {
  border-bottom: 1px solid #c8c8cd;
  height: 48px;
}

.poupSearch .mint-tab-container li {
  display: flex;
  flex-wrap: wrap;
}

.poupSearch .mint-tab-container li div {
  flex: 50%;
  height: 24px;
  line-height: 24px;
  overflow: hidden;
  text-overflow: clip;
}

.poupSearch .mint-tab-container li div:nth-child(1) {
  flex: 40%;
  height: 24px;
  line-height: 24px;
  overflow: hidden;
  text-overflow: clip;
  padding-right: 10%;
}

.poupSearch .mint-navbar .mint-tab-item {
  padding: 0;
}

.poupSearch .mint-navbar .mint-tab-item.is-selected {
  color: white;
}

.poupSearch .useHistory li {
  display: flex;
  align-items: center;
  font-size: 14px;
}

.poupSearch .useHistory li>span {
  position: relative;
}

.color1 {
  color: #969696
}

.color1 {
  color: black
}

.ffixed {
  position: absolute !important;
}
</style scoped>



